  display: flex;
  justify-content: space-between;
  align-items: end;
  position: fixed;
  z-index: 31;
  left: 0;
  bottom: 0;
  right: 0;
  padding: 24px;
  // background: red;
  .box-items {
    width: 100px;
    border: 4px solid red;
    border-radius: 8px;
    .box-item {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100px;
      &:not(:last-of-type) {
        border-bottom: 4px solid red;
      }
      img {
        width: 80px;
        height: 80px;
      }
      .left-time {
        position: absolute;
        right: 10px;
