<template>
  <section class="pop-start"
    v-show="isShow">
    <div class="box-logo">
      <img src="https://gitee.com/youngbeen/zombies-in-my-pocket/raw/master/src/assets/game_logo.png" alt="">
    </div>
    <div class="primary-red">Web Edition</div>
    <div class="box-creator">
      <div class="text-center">原桌游由Jeremiah Lee设计。为了更好的可玩性，Youngbeen基于原游戏设计做了一些调整，并实现了此程序版本</div>
      <div class="text-center">All game program copyright reserved by Youngbeen 2024-present</div>
    </div>
    <div class="desc style-font" :data-content="desc">
      {{ desc }}
    </div>
    <div class="select-player text-center">
      <div class="box-player"
        :class="[selectedPlayer === '1' && 'active']"
        @click="togglePlayer('1')">
        <img class="avatar" src="https://gitee.com/youngbeen/zombies-in-my-pocket/raw/master/src/assets/char_backpacker.jpg" alt="">
      </div>
      <div class="box-player"
        :class="[selectedPlayer === '2' && 'active']"
        @click="togglePlayer('2')">
        <img class="avatar" src="https://gitee.com/youngbeen/zombies-in-my-pocket/raw/master/src/assets/char_sportgirl.jpg" alt="">
      </div>
      <div class="box-player"
        :class="[selectedPlayer === '3' && 'active']"
        @click="togglePlayer('3')">
        <img class="avatar" src="https://gitee.com/youngbeen/zombies-in-my-pocket/raw/master/src/assets/char_oldman.jpg" alt="">
      </div>
      <div class="box-player"
        :class="[selectedPlayer === '4' && 'active']"
        @click="togglePlayer('4')">
        <img class="avatar" src="https://gitee.com/youngbeen/zombies-in-my-pocket/raw/master/src/assets/char_soldier.jpg" alt="">
      </div>
    </div>
    <div class="char-desc">
      {{ characterDesc }}
    </div>
    <div class="text-center" style="margin-top: 40px;">
      <button class="primary-button start-button"
        @click="start()">开始</button>
    </div>
  </section>
</template>

<script>
import eventBus from '@/EventBus'
import player from '@/models/Player'

const PlayerMap = {
  1: 'backpacker',
  2: 'sportgirl',
  3: 'oldman',
  4: 'soldier'
}
const characterDescMap = {
  1: '旅行者：身上可以多携带一件物品，初始携带一根蜡烛',
  2: '健身女孩：行动敏捷，在最终时间前能完成更多事件。初始携带一罐苏打水',
  3: '中年男人：初始携带一桶汽油',
  4: '特种兵：初始携带一枚手雷，可炸死一个房间的所有丧尸'
}
export default {
  data () {
    return {
      isShow: true,
      desc: '丧尸到处游荡，你必须在午夜12点的钟声前，找到有邪恶祭坛的房子，拿到邪恶图腾并带到外面的墓地将其埋入土中才能令逝者安息...',
      selectedPlayer: '1'
    }
  },
  computed: {
    characterDesc () {
      return characterDescMap[this.selectedPlayer]
    }
  },

  mounted () {
    eventBus.$on('notifyMessageAction', ({ action, tag }) => {
      if (action === 'confirm' && tag && tag.source === 'restart') {
        this.isShow = true
      }
    })
  },

  beforeDestroy () {
    eventBus.$off('notifyMessageAction')
  },

  methods: {
    togglePlayer (val) {
      this.selectedPlayer = val
    },
    start () {
      player.character = PlayerMap[this.selectedPlayer]
      this.isShow = false
      this.selectedPlayer = '1'
      eventBus.$emit('notifyStart')
    }
  }
}
</script>

